.App {
  text-align: center;
}
body {
  font-family: Arial, sans-serif;
 
background-color: #962424;
color: whitesmoke;


}
nav{
  background-color: rgb(135, 151, 222);
  text-align: center;
  color: antiquewhite;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav li {
  display: inline;
  margin: 0 10px;
  text-align: center;
}

nav a {
  text-decoration: none;
  color: #333;
  
}

h1, h2 {
  color: #333;
  text-align: center;
}
p{
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
